<template>
  <div class="container">
    <page-top page-name="角色信息" :links="links" :can-back="true"></page-top>
    <div class="container-content">
      <div class="container-header">
        <div class="header-title">
          <div class="header-left">基本信息</div>
        </div>
        <div class="header-info">
          <el-row :gutter="20">
            <el-col v-for="obj in infoList" :key="obj.prop" :span="6">
              <CtTooltip :label="obj.label" :content="itemInfoFormatter(obj)"></CtTooltip>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="container-bottom">
        <div class="container-header">
          <div class="header-title">
            <div class="header-left">权限信息</div>
          </div>
        </div>
        <div class="container-form">
          <MenuTree v-loading="loading" :tree-data="treeData" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import * as utils from '../utils'
import fetch from '@/request/fetch'
import MenuTree from '../components/MenuTree'
export default {
  components: {
    MenuTree,
  },
  data() {
    return {
      importTaskId: '',
      links: [{ name: '用户管理' }, { name: '角色详情' }],
      infoList: [
        {
          label: '角色名称',
          prop: 'roleName',
        },
        {
          label: '角色类型',
          prop: 'roleType',
        },
        {
          label: '企业客户',
          prop: 'custName',
        },
        {
          label: '渠道商',
          prop: 'channelName',
        },
        {
          label: '角色描述',
          prop: 'remark',
        },
      ],
      tableData: [],
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      detailInfo: {},
      treeData: [],
      activeTab: '1',
      changePackageDialogVisible: false,
      changeAttrDialogVisible: false,
      qryParam: {
        accNum: '',
      },
    }
  },
  created() {
    this.querySysRoleDetail()
    this.menuAuthorization()
  },
  methods: {
    async querySysRoleDetail() {
      const param = {
        roleId: this.$route.query.roleId,
      }
      const res = await fetch('/sysrole/querySysRoleDetail', param, { method: 'get' })
      this.detailInfo = res
    },
    async menuAuthorization() {
      const param = { roleId: this.$route.query.roleId }
      this.loading = true
      const res = await fetch('/sysrole/menuAuthorization', param, { method: 'get' })
      this.loading = false
      this.treeData = utils.convert2UI(res)
    },
    itemInfoFormatter(obj) {
      const des = obj.formatter
        ? obj.formatter(this.detailInfo[obj.prop])
        : this.detailInfo[obj.prop] || '-'
      return des
    },
    changePackage() {
      this.changePackageDialogVisible = true
    },
    changeAttr() {
      this.changeAttrDialogVisible = true
    },
    reset() {
      this.$refs.qryParamRef.resetFields()
      this.qryCmpParamPage(false)
    },
    addParam() {},
  },
}
</script>
