import { deepClone } from '@/utils/copyUtil'

function setMenuName(node) {
  node.menuName = node.menuName || node.privName
  if (node.children && node.children.length > 0) {
    node.children.forEach((item) => setMenuName(item))
  }
}

export function convert2UI(list) {
  const checked = list.every((item) => item.checked === true)
  const rootNode = { menuName: '全选', privId: 666666, checked, children: list }
  setMenuName(rootNode)
  return [rootNode]
}

export function convert2Server(checkedKeys, list) {
  // debugger
  const treeNodes = deepClone(list)
  function traverse(nodes) {
    for (const node of nodes) {
      if (checkedKeys.includes(node.privId)) {
        node.checked = true
      } else {
        node.checked = false
      }
      if (node.children && node.children.length > 0) {
        traverse(node.children)
      }
    }
  }
  traverse(treeNodes)
  return treeNodes[0].children
}

