<template>
  <el-tree
    ref="tree"
    :data="treeData"
    show-checkbox
    node-key="privId"
    :check-strictly="true"
    :props="treeProps"
    :default-checked-keys="defaultCheckedKeys"
    :default-expanded-keys="[666666]"
    @check-change="checkChange"
  >
  </el-tree>
</template>

<script>
import { deepClone } from '@/utils/copyUtil'

export default {
  props: {
    treeData: {
      type: Array,
      default: () => {},
    },
  },
  data() {
    return {
      treeProps: {
        label: 'menuName',
      },
      manulCheck: true,
    }
  },
  computed: {
    defaultCheckedKeys() {
      const checkedIds = []
      function traverse(nodes) {
        for (const node of nodes) {
          if (node.checked) {
            checkedIds.push(node.privId)
          }
          if (node.children && node.children.length > 0) {
            traverse(node.children)
          }
        }
      }
      traverse(this.treeData ?? [])
      return checkedIds
    },
  },
  methods: {
    checkChange(node, checked) {
      if (!this.manulCheck) {
        return
      }
      this.manulCheck = false
      // 全部取消/选择
      const setChecked = (arr, ifChecked) => {
        arr?.forEach((item) => {
          this.$refs.tree.setChecked(item.privId, ifChecked)
          if (item?.children?.length) {
            setChecked(item?.children, ifChecked)
          }
        })
      }
      // 如果为取消
      if (checked === false) {
        // 如果当前节点有子集
        // 循环子集将他们的选中取消
        setChecked(node?.children ?? [], false)
      } else {
        // 否则(为选中状态)
        // 判断父节点id是否为空
        setChecked(node?.children ?? [], true)
      }
      this.setFatherNodeStatus()
      setTimeout(() => {
        this.manulCheck = true
      }, 1)
    },
    setFatherNodeStatus() {
      const checkedKeys = this.$refs.tree.getCheckedKeys()
      console.log('++++', checkedKeys)
      const allNodes = deepClone(this.treeData)

      function setCheckedStatus(allNodes) {
        // 递归函数，用于设置 checked 状态
        function recursiveSetChecked(node) {
          // 如果当前节点有 children，继续递归处理
          if (node.children && node.children.length > 0) {
            let allUnchecked = true

            // 遍历子节点
            node.children.forEach((child) => {
              recursiveSetChecked(child) // 先递归处理子节点
              if (checkedKeys.includes(child.privId)) {
                allUnchecked = false
              }
            })

            // 如果所有子节点都被选中，则父节点 checked 为 true
            // 如果所有子节点都未被选中，则父节点 checked 为 false
            if (!allUnchecked) {
              checkedKeys.push(node.privId)
            }
          }
        }

        // 首先，将所有节点的 checked 状态设为 true（或不改变）
        allNodes.forEach((item) => {
          recursiveSetChecked(item)
        })
      }
      // 调用函数设置 checked 状态
      setCheckedStatus(allNodes)
      console.log('----', checkedKeys)
      this.$refs.tree.setCheckedKeys(checkedKeys)
    },
  },
}
</script>

